:root {
  --main-orange: #FE7865;
  --hover-orange: #F17D4B;
  --basic-black: #141517;
  --light-black: #2C2E3F;
  --background-black: #1D1E25;
  --light-black-border: rgba(44, 46, 63, 0.3);
  --text-black: #48494D;
  --hover-black: rgba(20, 21, 23);
  --text-gray: rgba(44, 46, 63, 0.7);
  --background-gray: #FBF8F8;
  --basic-white: #FFFFFF;
  --default-black: #000000;
  --shadow-black: rgba(41, 54, 66, 0.1);
  --light-gray: rgba(20, 21, 23, 0.5);
  --footer-border: #D7D9DF;
}

.page__body {
  font-family: "Inter", "arial", sans-serif;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 28px;
  background-color: var(--basic-white);
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

header {
  position: fixed;
  background-color: var(--basic-white);
  z-index: 100;
  width: 100%;
  box-shadow: 0 1px 9px -3px rgba(0, 0, 0, 0.4);
}

.section-container {
  margin: 0 20px;
}
@media (min-width: 1000px) {
  .section-container {
    max-width: 1170px;
    margin: 0 auto;
  }
}

.block-wrapper {
  margin-top: 50px;
}
@media (min-width: 1000px) {
  .block-wrapper {
    margin-top: 100px;
  }
}

.main-nav {
  min-height: 50px;
}
@media (min-width: 1000px) {
  .main-nav {
    padding: 26px 0 16px 0;
    min-height: 42px;
  }
}

.main-nav__logo {
  width: 44px;
  height: 17px;
  left: 16px;
  top: 18px;
  z-index: 2;
}
@media (min-width: 768px) {
  .main-nav__logo {
    left: 21px;
  }
}
@media (min-width: 1000px) {
  .main-nav__logo {
    width: 147px;
    height: 42px;
    left: 0;
    top: 0;
  }
}

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 50px;
  z-index: 3;
  border: none;
  background-color: var(--main-orange);
}
.main-nav__toggle::before {
  content: "";
  background-image: url("/img/toggle-cross.svg");
  background-repeat: no-repeat;
  background-color: inherit;
  position: absolute;
  top: 12px;
  right: 15px;
  z-index: 2;
  display: block;
  width: 22px;
  height: 22px;
  cursor: pointer;
  border: none;
}
@media (min-width: 1000px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
}

.main-nav--closed .main-nav__toggle {
  background-color: var(--main-orange);
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  width: 23px;
  height: 1px;
  background-color: var(--basic-white);
  position: absolute;
  top: 15px;
  right: 14px;
  box-shadow: 0 8px 0 0 var(--basic-white), 0 16px 0 0 var(--basic-white);
}
@media (max-width: 999px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__toggle {
    width: 72px;
  }
  .main-nav--closed .main-nav__toggle::before {
    right: 27px;
  }
}

@media (max-width: 999px) {
  .main-nav--opened .main-nav__toggle {
    background-color: inherit;
  }
  .main-nav--opened .main-nav__list {
    width: 100%;
    padding-bottom: 284px;
  }
}
@media (max-width: 999px) and (min-width: 768px) {
  .main-nav--opened .main-nav__toggle {
    width: 72px;
  }
  .main-nav--opened .main-nav__toggle::before {
    right: 27px;
  }
}

@media (min-width: 1000px) {
  .main-nav__wrapper {
    max-width: 1170px;
    margin: 0 auto;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.main-info {
  padding-top: 50px;
}
@media (min-width: 1000px) {
  .main-info {
    padding-top: 144px;
  }
}
@media (min-width: 1000px) {
  .main-info__wrapper {
    display: flex;
    justify-content: space-between;
  }
}
.main-info__left-header {
  font-size: 32px;
  font-style: normal;
  font-weight: 600;
  line-height: 120%;
  letter-spacing: -3px;
  margin: 0 0 40px;
}
@media (min-width: 1000px) {
  .main-info__left-header {
    font-size: 54px;
    font-weight: 700;
    margin: 0 0 80px;
  }
}
.main-info__left-subscription {
  display: flex;
  margin-bottom: 32px;
}
.main-info__left-subscription-line {
  margin-top: 20px;
  margin-right: 32px;
  width: 90px;
  align-self: center;
  border-bottom: 1px solid black;
}
.main-info__left-subscription-text {
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 130%;
}
@media (min-width: 1000px) {
  .main-info__left-subscription-text {
    font-size: 32px;
  }
}
@media (min-width: 1000px) {
  .main-info__left-subscription {
    margin-bottom: 0;
  }
}
.main-info__right-additional-text {
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 32px;
  margin-top: 16px;
}
.main-info__right-picture-container {
  margin: 0 auto;
  width: 320px;
  height: 240px;
}
@media (min-width: 1000px) {
  .main-info__right-picture-container {
    margin: unset;
    width: 475px;
    height: 340px;
  }
}
.main-info__right-picture-container img {
  width: 100%;
  height: 100%;
}

.orange-blur {
  border-radius: 551px;
  opacity: 0.1299999952;
  background: #FA7515;
  filter: blur(110px);
  width: 300px;
  height: 300px;
  position: absolute;
}
@media (min-width: 1000px) {
  .orange-blur {
    width: 551px;
    height: 551px;
  }
}

.services {
  padding-top: 100px;
}
@media (min-width: 1000px) {
  .services {
    padding-top: 200px;
  }
}
.services__header {
  font-size: 32px;
  font-style: normal;
  font-weight: 600;
  line-height: 120%;
  /* 67.2px */
  letter-spacing: -2px;
}
@media (min-width: 1000px) {
  .services__header {
    font-size: 56px;
  }
}
.services__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 64px;
  row-gap: 64px;
}
.services__item {
  display: flex;
  flex-direction: column;
  height: 550px;
  padding: 10px 10px;
  border: 1px solid #D9D9D9;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  width: 340px;
}
@media (min-width: 1000px) {
  .services__item {
    height: 754px;
    width: 465px;
    padding: 24px 32px;
  }
}
.services__item-header {
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 130%;
  letter-spacing: -1px;
  margin: 16px 0;
}
@media (min-width: 1000px) {
  .services__item-header {
    font-size: 32px;
  }
}
.services__item-picture {
  align-self: center;
}
.services__item-description {
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}
@media (min-width: 1000px) {
  .services__item-description {
    font-size: 16px;
  }
}
.services__item-description-list {
  list-style: disc;
  margin-left: 24px;
}
.services__item-button {
  margin-top: auto;
  padding: 8px 16px;
  width: 180px;
  border-radius: 100px;
  background: var(--neutral-100, #1D1E25);
  color: var(--basic-white);
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
  border: none;
  align-self: center;
  cursor: pointer;
  text-align: center;
}
.services__item-button:hover, .services__item-button:focus {
  opacity: 0.9;
}
.services__item-button:active {
  opacity: 0.8;
}
@media (min-width: 1000px) {
  .services__item-button {
    width: 360px;
  }
}

.work-examples {
  padding: 100px 0;
}
@media (min-width: 1000px) {
  .work-examples {
    padding: 200px 0;
  }
}
.work-examples__header {
  font-size: 32px;
  font-style: normal;
  font-weight: 600;
  line-height: 120%;
  letter-spacing: -2px;
}
@media (min-width: 1000px) {
  .work-examples__header {
    font-size: 56px;
  }
}
.work-examples__list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 17%;
  row-gap: 80px;
  justify-content: center;
}
.work-examples__image-container img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.slider-container {
  position: relative;
  width: 320px;
  height: 440px;
  overflow: hidden;
}
@media (min-width: 1000px) {
  .slider-container {
    width: 480px;
    height: 576px;
  }
}

.slider {
  display: flex;
  height: 100%;
  width: 100%;
}

.prev-button,
.next-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 50px;
  border: none;
  font-size: 24px;
  color: var(--basic-white);
  background: rgba(0, 0, 0, 0.5);
}
.prev-button:hover,
.next-button:hover {
  background: rgba(0, 0, 0, 0.7);
}

.prev-button {
  left: 0;
}

.next-button {
  right: 0;
}

.about-company {
  background-color: var(--background-black);
  color: var(--basic-white);
  padding: 100px 0;
}
@media (min-width: 1000px) {
  .about-company {
    padding: 200px 0;
  }
}
.about-company__header {
  font-size: 32px;
  font-style: normal;
  font-weight: 600;
  line-height: 120%;
  letter-spacing: -2px;
}
@media (min-width: 1000px) {
  .about-company__header {
    font-size: 56px;
  }
}
.about-company__picture {
  background-image: url("/img/about-company-background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 200px;
  margin-bottom: 20px;
}
@media (min-width: 1000px) {
  .about-company__picture {
    background-size: auto;
    background-position-y: -300px;
    height: 338px;
    margin-bottom: 80px;
  }
}
.about-company__description {
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
}
@media (min-width: 1000px) {
  .about-company__description {
    font-size: 16px;
  }
}
.about-company__advantages {
  margin-top: 40px;
}
@media (min-width: 1000px) {
  .about-company__advantages {
    margin-top: 80px;
  }
}
.about-company__advantages-header {
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 42px;
  /* 131.25% */
  letter-spacing: -1px;
}
@media (min-width: 1000px) {
  .about-company__advantages-header {
    font-size: 32px;
  }
}
.about-company__advantages-list {
  display: flex;
  flex-wrap: wrap;
  gap: 14%;
  row-gap: 40px;
  justify-content: center;
}
@media (min-width: 1000px) {
  .about-company__advantages-list {
    row-gap: 20px;
  }
}
.about-company__advantages-item {
  width: 80%;
  border: 1px solid var(--basic-white);
  padding: 15px;
}
@media (min-width: 1000px) {
  .about-company__advantages-item {
    width: 40%;
  }
}
.about-company__advantages-item-header {
  font-size: 16px;
  position: relative;
  margin-left: 30px;
}
.about-company__advantages-item-header::before {
  content: "";
  position: absolute;
  width: 22px;
  height: 22px;
  left: -30px;
  background-size: cover;
  top: 2px;
}
@media (min-width: 1000px) {
  .about-company__advantages-item-header {
    font-size: 24px;
    margin-left: 40px;
  }
  .about-company__advantages-item-header::before {
    width: 28px;
    height: 28px;
    top: unset;
    left: -40px;
  }
}
.about-company__advantages-item .service-guaranty::before {
  background-image: url("/img/icon-service-garanty.svg");
}
.about-company__advantages-item .best-price::before {
  background-image: url("/img/icon-best-price.svg");
}
.about-company__advantages-item .short-date::before {
  background-image: url("/img/icon-short-date.svg");
}
.about-company__advantages-item .save-materials::before {
  background-image: url("/img/icon-save-materials.svg");
}
.about-company__advantages-item-description {
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 26px;
}
@media (min-width: 1000px) {
  .about-company__advantages-item-description {
    font-size: 16px;
  }
}

.page-footer {
  border-top: 1px solid var(--basic-white);
  padding-top: 100px;
  background-color: var(--background-black);
  color: var(--basic-white);
  padding-bottom: 58px;
}
.page-footer__top-container {
  display: flex;
  flex-direction: column;
  row-gap: 24px;
  width: fit-content;
  margin: 0 auto 20px;
}
.page-footer__logo {
  color: white;
}
.page-footer__phone {
  margin-left: 32px;
  position: relative;
  color: inherit;
}
.page-footer__phone::before {
  content: "";
  position: absolute;
  width: 25px;
  height: 25px;
  background-image: url("/img/icon-tel.svg");
  background-repeat: no-repeat;
  left: -32px;
}
.page-footer__phone:hover {
  opacity: 0.7;
}
.page-footer__social-list {
  display: flex;
  column-gap: 8px;
  justify-content: center;
}
.page-footer__social-item {
  width: 30px;
  height: 30px;
  background-repeat: no-repeat;
}
.page-footer .whatsapp {
  background-image: url("/img/icon-whatsapp.svg");
}
.page-footer .viber {
  background-image: url("/img/icon-viber.svg");
}
.page-footer .telegram {
  background-image: url("/img/icon-tg.svg");
}
.page-footer__location {
  position: relative;
  text-align: center;
}
.page-footer__location::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 30px;
  left: 10px;
  background-image: url("/img/icon-location.svg");
  background-repeat: no-repeat;
  background-size: contain;
}
.page-footer__bottom-container {
  border-top: 1px solid var(--basic-white);
  padding-top: 20px;
}
.page-footer__copyright {
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  display: block;
  margin: 0 auto;
  width: fit-content;
}

.site-list {
  list-style: none;
}
@media (min-width: 1000px) {
  .site-list {
    right: 0;
    display: flex;
    column-gap: 24px;
    justify-content: space-between;
    flex-wrap: nowrap;
  }
}

.site-list a {
  display: block;
  text-align: left;
  color: var(--text-black);
  font-weight: 600;
  font-size: 20px;
  padding: 16px 17px;
  margin-bottom: 30px;
  border-bottom: 1px solid var(--shadow-black);
}
@media (min-width: 1000px) {
  .site-list a {
    margin: 0;
    font-size: 16px;
    line-height: 150%;
    font-weight: 500;
    padding: 0;
    background-color: inherit;
    border-bottom: none;
  }
}

.site-list__item {
  margin-bottom: 30px;
}
.site-list__item:hover a {
  color: var(--main-orange);
}
.site-list__item:last-child {
  margin-bottom: 0;
}
@media (min-width: 1000px) {
  .site-list__item {
    margin: 0 0 0 40px;
    position: relative;
  }
}

.site-list__phone {
  position: relative;
}
.site-list__phone::before {
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  background-image: url(/img/icon-tel-black.svg);
  background-repeat: no-repeat;
  left: -32px;
  top: 4px;
  background-size: cover;
}
@media (min-width: 1000px) {
  .site-list__phone {
    display: block;
    cursor: pointer;
    position: relative;
  }
  .site-list__phone:hover {
    color: var(--main-orange);
  }
}

.modal__form {
  width: 320px;
  height: 568px;
  position: fixed;
  top: 25%;
  left: 0;
  right: 0;
  margin: auto;
  background-color: var(--basic-white);
  text-align: center;
}
@media (min-width: 768px) {
  .modal__form {
    width: 720px;
    height: 444px;
  }
}

.modal__image {
  display: none;
}
@media (min-width: 768px) {
  .modal__image {
    display: block;
    position: fixed;
    width: 334px;
    height: 444px;
    background-image: url("/img/modal-ocean.jpg");
  }
}

@media (min-width: 768px) {
  .modal__container {
    margin: 0;
    width: 386px;
    margin-left: 335px;
  }
}

.modal__close {
