  top: 20px;
  right: 20px;
  border: none;
  width: 22px;
  height: 22px;
  background-color: var(--basic-white);
}
.modal__close::before {
  content: "";
  background-image: url("/img/toggle-cross.svg");
  background-repeat: no-repeat;
  background-color: var(--basic-white);
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 22px;
  height: 22px;
  cursor: pointer;
  border: none;
